"use client";
import { useEffect, useState, useRef } from "react";
import { fetchMessages } from "@/services/api";
import LoadingDots from "./LoadingDots";
import { io, Socket } from "socket.io-client";

export default function ChatBox() {
  const [messages, setMessages] = useState<any[]>([]);
  const [input, setInput] = useState("");
  const [aiTyping, setAiTyping] = useState(false);
  const socketRef = useRef<Socket | null>(null);
  const token = typeof window !== "undefined" ? localStorage.getItem("token") : null;

  // Connect socket.io on mount
  useEffect(() => {
    if (!token) return;

    const socket = io("http://localhost:5000", {
      auth: { token },
    });

    socketRef.current = socket;

    // Initial fetch
    fetchMessages(token).then(setMessages);

    // Listen for new messages
    socket.on("newMessage", (msg) => {
      setMessages((prev) => [...prev, msg]);
    });

    // Listen for AI typing
    socket.on("aiTyping", (typing: boolean) => {
      setAiTyping(typing);
    });

    socket.on("connect_error", (err) => {
      console.error("Socket error:", err.message);
    });

    return () => {
      socket.disconnect();
    };
  }, [token]);

  const handleSend = () => {
    if (!input.trim() || !socketRef.current) return;

    socketRef.current.emit("sendMessage", input.trim());
    setInput("");
  };

  return (
    <div className="p-4 h-screen flex flex-col">
      <div className="flex-1 overflow-auto border mb-2 p-2 space-y-2">
        {messages.map((msg, idx) => (
          <div
            key={idx}
            className={`p-2 rounded-md max-w-sm ${
              msg.sender && msg.sender.username
                ? "bg-blue-100 text-right ml-auto"
                : "bg-gray-100 text-left"
            }`}
          >
            {msg.content}
          </div>
        ))}
        {aiTyping && (
          <div className="p-2 bg-gray-200 rounded-md max-w-sm text-left">
            <LoadingDots />
          </div>
        )}
      </div>
      <div className="flex">
        <input
          value={input}
          onChange={(e) => setInput(e.target.value)}
          className="flex-1 border p-2"
          placeholder="Type a message..."
        />
        <button
          onClick={handleSend}
          className="bg-blue-600 text-white px-4 py-2 ml-2"
        >
          Send
        </button>
      </div>
    </div>
  );
}
